import React, { useState, useEffect } from 'react'

import PersonList from './PersonList'
import PersonForm from './PersonForm'
import PersonFilter from './PersonFilter'
import Notification from './Notification'
import personService from './services/personService'

import axios from 'axios'

const App = () => {

  const [persons, setPersons] = useState([])
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [notificationMessage, setNotificationMessage] = useState(null)
  const [notificationStatus, setNotificationStatus] = useState(null)

  useEffect(() => {
    personService.getAll().then(allPersons => setPersons(persons.concat(allPersons)))
  }, [])

  const handleNameChange = (event) => {
    console.log(event.target.value)
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    console.log(event.target.value)
    setNewNumber(event.target.value)
  }

  const addPerson = (event) => {
    event.preventDefault()

    if (persons.filter(person => person.name.toLowerCase() === newName.toLowerCase()).length !== 0) {
      const confirmed = window.confirm(`${newName} is already added to phonebook, replace the old number with a new one`)

      if (confirmed) {

        const personToUpdate = persons.filter(person => person.name.toLowerCase() === newName.toLowerCase())[0]

        const newPersonObject = {
          name: personToUpdate.name,
          number: newNumber,
          id: personToUpdate.id
        }

        personService.update(personToUpdate.id, newPersonObject).
          then(response => {
            setPersons(persons.map(person => person.id === personToUpdate.id ? newPersonObject : person))

            setNotificationMessage(
              `Updated ${newPersonObject.name}'s phone number`
            )
            setNotificationStatus('success')
            setTimeout(() => {
              setNotificationMessage(null)
              setNotificationStatus(null)
            }, 5000)

          }).catch(error => {
            setNotificationMessage(
              `${newPersonObject.name} has already been removed from server`
            )
            setNotificationStatus('error')
            setTimeout(() => {
              setNotificationMessage(null)
              setNotificationStatus(null)
            }, 5000)
          })
      }
    } else {

      const personObject = {
        name: newName,
        number: newNumber,
      }
      personService.create(personObject).then(newPerson => {
        setPersons(persons.concat(newPerson))
      }).catch(error => {
        let message = error.response.data.error
        setNotificationMessage(
          `${message}`
        )
        setNotificationStatus('error')
        setTimeout(() => {
          setNotificationMessage(null)
          setNotificationStatus(null)
        }, 5000)

      })
      setNewName('')
      setNewNumber('')

      setNotificationMessage(
        `Added ${personObject.name}`
      )
      setNotificationStatus('success')
      setTimeout(() => {
        setNotificationMessage(null)
        setNotificationStatus(null)
      }, 5000)
    }
  }

  const removePerson = (id) => {
    const personToDelete = persons.filter(person => person.id === id)[0]
    const confirmed = window.confirm(`Delete ${personToDelete.name}?`)
    if (confirmed) {
      personService.remove(id).then(response => {
        setPersons(persons.filter(person => person.id !== personToDelete.id))

        setNotificationMessage(
          `Removed ${personToDelete.name}`
        )
        setNotificationStatus('success')
        setTimeout(() => {
          setNotificationMessage(null)
          setNotificationStatus(null)
        }, 5000)

      })
    }
  }

  const [nameFilter, setNameFilter] = useState('')

  const handleFilterChange = (event) => {
    console.log(event.target.value)
    setNameFilter(event.target.value)
  }

  const personsToShow = nameFilter !== '' ? persons.filter(person => person.name.toLowerCase().includes(nameFilter)) : persons

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={notificationMessage} status={notificationStatus} />
      <PersonFilter nameFilter={nameFilter} handleFilterChange={handleFilterChange} />
      <h2>add a new</h2>
      <PersonForm
        newName={newName}
        newNumber={newNumber}
        handleNameChange={handleNameChange}
        handleNumberChange={handleNumberChange}
        addPerson={addPerson}
      />
      <h2>Numbers</h2>
      <PersonList persons={personsToShow} removePerson={removePerson} />
    </div>
  )

}

export default App

