import React from 'react'

const Notification = ({ message, status }) => {

    const notificationStyle = {
        background: 'lightgrey',
        fontSize: 20,
        borderStyle: 'solid',
        borderRadius: 5,
        padding: 10,
        marginBottom: 10,
    }

    const errorStyle = {
        color: 'red',
    }

    const successStyle = {
        color: 'green',

    }

    if (message === null) {
      return null
    }

    const statusStyle = status === 'error' ? errorStyle : successStyle
  
    return (
      <div className="error" style={{...notificationStyle, ...statusStyle}}>
        {message}
      </div>
    )
}

export default Notification