import React from 'react'

const PersonList = (props) => {

    const { persons, removePerson } = props

    return (
        <div>
            {
                persons.map(person => <p key={Math.floor(Math.random() * Math.floor(10000000))}>{person.name} {person.number} <button onClick={() => removePerson(person.id)}>delete</button></p>)
            }
        </div>
    )
}

export default PersonList

